import { createWebHistory, createRouter } from "vue-router";
import list from './components/MainList.vue';
import page from './components/ListPage.vue';

const routes = [
  {
    path: '/home',
    component: list,
  },
  {
    path: '/:id',
    component: page,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 